@import url(//fonts.googleapis.com/css?family=Lato:300:400);


.app{
  display: flex;
  justify-content: center;
  width: 100vw;
  height: 80vh;
  position: absolute;
  z-index: 1;
}

.todo{
  margin-top: 2rem;
  margin-bottom: auto; /* para fazer com que a caixa desca conforme o conteudo */
  display: flex;
  flex-direction: column;
  padding: 2rem;
  width: 400px;
  background-color: rgb(255, 255, 255);
  box-shadow: 4px 4px 15px 1px rgba(0,0,0,0.3);
}

header{
  display: flex;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  border-bottom: 1px solid rgb(230, 230, 230);
  padding-bottom: 2rem;
}

/* Estilizando Botão */

header button{
  background-color: #fb6c6f;
  padding: 0.6rem;
  border-radius: 5px;
}

header button:hover{
  -webkit-filter: brightness(0.90);
          filter: brightness(0.90);
  transition: 0.4s;
}

header button svg{
  font-size: 1.5rem;
  color: #fff;
}

/* Estilizando Input */

header input{
  padding: 1rem 2rem;
  background-color: rgb(244, 243, 243);
  width: 300px;
  border-radius: 5px;
  font-family: Raleway;
  font-size: 1.2rem;
}

/* Estilizando Taks */

.task-container{
  margin-top: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;

}

.check-and-title{
  display: flex;
  align-items: center;
}

.checkbox-container{
  position: relative;
  padding-left: 35px;
  margin-bottom: 24px;
  cursor: pointer;
}

.checkbox-container input{
  position: absolute;
  cursor: pointer;
  opacity: 0;
  height: 0;
  width: 0;
}

.checkmark{
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  border: 1px solid rgba(0,0,0,0.1);
  border-radius: 5px;
}

/* Mudar o checkbox de cor ao clicar nele (checked) */

.checkbox-container input:checked ~ .checkmark{
  background-color: #fb6c6f;
}

.checkmark::after{
  content: "";
  position: absolute;
  display: none;
}

.checkbox-container input:checked ~ .checkmark::after{
  display: block;
}

.checkbox-container .checkmark::after{
  left: 8px;
  top: 3px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.task-container p{
  margin-left: 0.6rem;
  font-size: 1.4rem;
}

.task-container.completed p{
  color: #909193;
  font-style:italic
}

.task-container svg{
  font-size: 1.6rem;
  color: #fd355a;
  cursor: pointer;
}
